#event-card {
	position: relative;
	height: 40vh;
	cursor: pointer;
}

@media (hover: hover) {
	#event-card:hover #short-description {
		transform-origin: bottom;
		transform: scaleY(1);
	}

	#event-card:hover :is(#event-title, #post-date) {
		opacity: 0;
	}
}

#post-date {
	position: absolute;
	bottom: 0;
	right: 0;
	padding: 0.5rem 1rem;
	transition: opacity 400ms ease-in-out;
	font-size: 0.85rem;
}

#event-image {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

#event-title {
	width: 50%;
	height: fit-content;
	text-align: center;
	padding: 1vh 1vw;
	transition: opacity 400ms ease-in-out;
}

#short-description {
	padding: 1vh 1vw;
	position: absolute;
	inset: 20% 0 0 0;
	transform-origin: bottom;
	transform: scaleY(0);
	transition: transform 400ms ease-in-out;
	font-size: 0.9rem;
}
